<template>
  <div>
    <div class="header_coupon">
      <van-nav-bar title="优惠券" left-arrow @click-left="onClickLeft">
        <van-icon name="ellipsis" slot="right" />
      </van-nav-bar>
    </div>
    <!-- 优惠券列表 -->

    <van-coupon-list
      :coupons="coupons"
      :chosen-coupon="chosenCoupon"
      :disabled-coupons="disabledCoupons"
      @change="onChange"
      @exchange="onExchange"
    />
  </div>
</template>

<script>
const coupon = {
  available: 1,
  condition: "无使用门槛\n最多优惠12元",
  reason: "",
  value: 150,
  name: "优惠券名称",
  startAt: 1489104000,
  endAt: 1514592000,
  valueDesc: "1.5",
  unitDesc: "元"
};
export default {
  data() {
    return {
      chosenCoupon: -1,
      coupons: [coupon],
      disabledCoupons: [coupon]
    };
  },

  methods: {
    onChange(index) {
      this.showList = false;
      this.chosenCoupon = index;
    },
    onExchange() {
      this.coupons.push(coupon);
    },
    onClickLeft() {
      this.$router.push("/wd");
    }
  }
};
</script>

<style lang="scss">
.header_coupon {
  .van-icon {
    color: black;
    font: 20px/1 "vant-icon";
  }
  .van-nav-bar .van-icon {
    color: black;
  }
}
</style>